#wrapper {
    overflow: hidden;
}

.body_lock {
    overflow: hidden;
}

.container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
}

/* ----------.main-section------------ */

.main-section {
    position: relative;
    min-height: 1785px;
    max-width: 2000px;
    margin: 0 auto;

    @include media ('<laptop') {
        min-height: 1205px;
    }

    @include media ('<widescreen') {
        min-height: 940px;
    }

    @include media ('<tablet') {
        min-height: 640px;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 450px;
        background: rgb(255, 255, 255);
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(254, 254, 254, 1) 100%);
        z-index: 4;

        @include media ('<tablet') {
            height: 200px;
        }

        @include media ('<tablet') {
            height: 150px;
            bottom: 5px;
        }
    }

    .text {
        position: absolute;
        text-align: center;
        z-index: 2;
        top: 415px;
        left: 50%;
        transform: translate(-50%);
        width: 100%;
        padding: 0 20px;

        @include media ('<laptop') {
            top: 220px;
        }

        @include media ('<widescreen') {
            top: 120px;
        }

        @include media ('<tablet') {
            top: 160px;
        }
    }

    .title {
        font-weight: 600;
        line-height: 1.2;
        color: #FEF5F5;
        margin-bottom: 10px;
        @include adaptiv-vaule("font-size", 128, 30, 1);

        @include media ('<phone') {
            font-size: 40px;
        }
    }

    .sub-title {
        font-weight: 600;
        color: #FEF5F5;
        margin: 0;
        @include adaptiv-vaule("font-size", 64, 20, 1);
    }

    #sky,
    #m1,
    #m2,
    #m3,
    #cloud1,
    #cloud2,
    #cloud3 {
        position: absolute;
    }

    #sky,
    #m1,
    #m2,
    #m3 {
        min-width: 2000px;
        left: 50%;
        transform: translate(-50%);

        @include media ('<laptop') {
            min-width: 1440px;
        }

        @include media ('<widescreen') {
            min-width: 1200px;
        }

        @include media ('<tablet') {
            min-width: 768px;
        }
    }

    #sky {
        top: 0;
    }

    #m1 {
        top: 325px;
        z-index: 1;

        @include media ('<laptop') {
            top: 235px;
        }

        @include media ('<widescreen') {
            top: 195px;
        }

        @include media ('<tablet') {
            top: 125px;
        }
    }

    #m2 {
        top: 475px;
        z-index: 3;

        @include media ('<laptop') {
            top: 350px;
        }

        @include media ('<widescreen') {
            top: 265px;
        }

        @include media ('<tablet') {
            top: 185px;
            z-index: 1;
        }
    }

    #m3 {
        top: 390px;
        z-index: 3;

        @include media ('<laptop') {
            top: 200px;
        }

        @include media ('<widescreen') {
            top: 100px;
        }
    }

    #cloud1 {
        min-width: 1100px;
        left: -60px;
        bottom: 340px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 600px;
            max-width: 600px;
            bottom: 240px;
        }

        @include media ('<widescreen') {
            min-width: 400px;
            max-width: 400px;
        }

        @include media ('<tablet') {
            min-width: 300px;
            max-width: 300px;
            bottom: 80px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
        }
    }

    #cloud2 {
        min-width: 1300px;
        left: -100px;
        bottom: -325px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 900px;
            max-width: 900px;
            bottom: -240px;
        }

        @include media ('<widescreen') {
            min-width: 700px;
            max-width: 700px;
            bottom: -240px;
        }

        @include media ('<tablet') {
            min-width: 500px;
            max-width: 500px;
            bottom: -140px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
            bottom: -70px;
        }
    }

    #cloud3 {
        min-width: 1300px;
        right: -300px;
        bottom: -15px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 1000px;
            max-width: 1000px;
        }

        @include media ('<widescreen') {
            min-width: 700px;
            max-width: 700px;
            bottom: -40px;
        }

        @include media ('<tablet') {
            min-width: 500px;
            max-width: 500px;
            right: -100px;
            bottom: -40px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
            bottom: 0px;
        }
    }
}

.about_us {
    /* min-height: 200vh; */
    padding: 80px 0;


    .content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media ("<tablet") {
            flex-direction: column-reverse;
        }
    }

    .img_block {
        width: 49%;
        position: relative;

        @include media ("<tablet") {
            width: 100%;
        }

        .skier {
            position: absolute;
            bottom: -15%;
            right: -20px;

            @include media ("<desktop") {
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    .text_block {
        max-width: 530px;
        margin-left: 35px;

        @include media ("<desktop") {
            max-width: 50%;
        }

        @include media ("<tablet") {
            margin: 0 0 30px 0;
            max-width: 100%;
            text-align: center;
        }
    }

    .title {
        margin-bottom: 25px;
    }

    .description {
        @include adaptiv-vaule("font-size", 20, 16, 1);
    }
}

.gallery {
    @include adaptiv-vaule("padding-top", 150, 30, 1);
    @include adaptiv-vaule("padding-bottom", 100, 50, 1);
    background: rgb(146, 146, 226);
    position: relative;

    &::before {
        content: '';
        position: absolute;
        top: -66px;
        left: 0;
        @include adaptiv-vaule("height", 150, 50, 1);
        width: 100vw;
        background: rgb(146, 146, 226);
        transform: skewY(-4deg);

        @include media ("<tablet") {
            top: -58px;
        }

        @include media ("<640px") {
            top: -48px;
        }

        @include media ("<phone") {
            top: -35px;
        }
    }

    .content {
        text-align: center;
    }

    .title {
        margin-bottom: 50px;
    }

    .gallery_block {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        row-gap: 40px;
        column-gap: 25px;

        @include media ("<960px") {
            grid-template-columns: 1fr 1fr;
            row-gap: 30px;
        }

        @include media ("<tablet") {
            row-gap: 25px;
        }
    }

    .set {
        position: relative;
        height: 400px;
        position: relative;
        border-radius: 15px;
        overflow: hidden;

        &:first-child {
            width: 100%;
            grid-column: span 2;
        }

        &:nth-child(n+6) {
            width: 100%;
            grid-column: span 2;
        }
    }
}